<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
  <div>
    <router-link :to="{name:'Statistics'}" style="text-decoration: none">
      <el-button size="medium">柱状图</el-button>
    </router-link>
    <div id="myChart" :style="{width:'1000px',height:'600px'}"></div>
  </div>
  </transition>
</template>

<script>
import gsap from "gsap";
const {pieVo} = require("@/api/login");
export default {
  name: "Pie",
  methods:{
    beforeEnter(el) {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateX(200px)'
      el.style.opacity = 0
    },

    enter(el, done) {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 0.5,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    }

  },
  mounted() {
    pieVo().then((response) => {
      console.log(response)
      let myChart = this.$echarts.init(document.getElementById('myChart'));
      myChart.setOption({
        backgroundColor: '#2c343c',
        title: {
          text: '产品销量统计',
          left: 'center',
          top: 20,
          textStyle: {
            color: '#ccc'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        visualMap: {
          show: false,
          min: 10,
          max: 1500,
          inRange: {colorLightness: [0, 1]}
        },
        series: [{
          name: '销量',
          type: 'pie',
          radius: '65%',
          center: ['50%', '50%'],
          data: response.data.sort((a, b) => {
            return a.value - b.value
          }),
          roseType: 'radius',
          label: {color: 'rgba(255,255,255,0.3)'},
          labelLine: {
            lineStyle: {color: 'rgba(255,255,255,0.3)'},
            smooth: 0.2,
            length: 10,
            length2: 20
          },
          itemStyle: {
            color: '#c23531',
            shadowBlur: 200,
            shadowColor: 'rgba(0,0,0,0.5)'
          },
          animationType: 'scale',
          animationEasing: 'elasticOut',
          animationDelay: function (idx) {
            return Math.random() * 300;
          }
        }]


      })

    })
  }
}
</script>

